/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from 'react'
import PropTypes from 'prop-types'
// import Helmet from 'react-helmet'
// import { Helmet } from 'react-helmet'
import { Helmet, HelmetProvider } from 'react-helmet-async'

function SEO({ description, title, url }) {
  return (
    <React.Fragment>
      <HelmetProvider>
        <Helmet>
          <title>{title}</title>
          <meta property={`description`} content={title} />
          <meta property="twitter:title" content={title} />
          <meta property={`twitter:card`} content={title} />
          <meta property={`twitter:site`} content="@gojektech" />
          <meta
            property={`twitter:image`}
            content="https://www.gojek.io/images/Go-Jek.png"
          />
          <meta property="twitter:description" content={description} />
          <meta property="og:title" content={title} />
          <meta property="og:url" content={url} />
          <meta
            property="og:image"
            content="https://www.gojek.io/images/Go-Jek.png"
          />
          <meta property="og:description" content={description} />
          <meta property="og:type" content={'website'} />
          <link
            rel="shortcut icon"
            href="/../images/favicon.png"
            type="image/x-icon "
          />
          <link rel="icon" href="./../images/favicon.png" type="image/x-icon" />
        </Helmet>
      </HelmetProvider>
    </React.Fragment>
  )
}

SEO.propTypes = {
  title: PropTypes.string.isRequired,
}

export default SEO



